import glamorous from 'glamorous';

const Error = glamorous.div({
  display: 'flex',
  justifyContent: 'center',
  margin: '5px',
  padding: '5px',
  alignItems: 'center',
  backgroundColor: '#f8d7da',
  border: '1px solid #f5c6cb',
  borderRadius: '5px',
  textAlign: 'center',
  color: '#721c24',
});

export default Error;



